import React, { useState } from "react";

import { Col, Container, Row } from "react-bootstrap";

import ProjectItem from "./ProjectItem";
import { SKILLSLIST, ROLELIST, PORTFOLIO } from "../../common/Const";
import FilterList from "./FilterList";

export default function Portfolio() {
  const [selectedRoles, setSelectedRoles] = useState([]);
  const [selectedSkills, setSelectedSkills] = useState([]);
  const [filteredProjects, setFilteredProjects] = useState(PORTFOLIO);

  const updateFilteredProjects = (roles, skills) => {
    setFilteredProjects(
      PORTFOLIO.filter((project) => {
        return (
          (roles.length === 0 ||
            roles.every((role) => project.roles.includes(role))) &&
          (skills.length === 0 ||
            skills.every((skill) => project.skills.includes(skill)))
        );
      })
    );
  };

  const onRoleClicked = (key) => {
    let roles = selectedRoles;
    if (selectedRoles.includes(key)) {
      roles = selectedRoles.filter((role) => role !== key);
      setSelectedRoles(selectedRoles.filter((role) => role !== key));
    } else {
      roles = [...selectedRoles, key];
      setSelectedRoles([...selectedRoles, key]);
    }

    updateFilteredProjects(roles, selectedSkills);
  };

  const onSkillClicked = (key) => {
    let skills = selectedSkills;
    console.log(selectedSkills);
    console.log(selectedRoles);
    if (selectedSkills.includes(key)) {
      skills = selectedSkills.filter((skill) => skill !== key);
      setSelectedSkills(selectedSkills.filter((skill) => skill !== key));
    } else {
      skills = [...selectedSkills, key];
      setSelectedSkills([...selectedSkills, key]);
    }

    updateFilteredProjects(selectedRoles, skills);
  };

  return (
    <Container>
      <div id="skills" className="text-center">
        <h1>Skills</h1>
      </div>
      <Container style={{ height: "fit-content", width: "100%" }}>
        <FilterList
          onClick={onRoleClicked}
          list={ROLELIST}
          selected={selectedRoles}
          background={"#dcecef"}
          color={"#02697b"}
          clickable={true}
        />
        <FilterList
          onClick={onSkillClicked}
          list={SKILLSLIST}
          selected={selectedSkills}
          background={"#fcf2d9"}
          color={"#976c02"}
          clickable={true}
        />
      </Container>
      <div id="portfolio" className="text-center mt-5">
        <h1>Portfolio</h1>
      </div>
      <Row>
        {filteredProjects.length > 0 ? (
          <>
            {filteredProjects.map((project, index) => (
              <Col md={6} className="text-center p-2" style={{ marginTop: 0 }}>
                <ProjectItem key={index} project={project} />
              </Col>
            ))}
          </>
        ) : (
          <>
            <Col className="text-center p-2" style={{ marginTop: 0 }}>
              <p
                style={{
                  marginTop: 15,
                  padding: 5,
                  color: "#a8a8a8",
                  fontSize: 18,
                }}
              >
                No projects found, try change the selected filters
              </p>
            </Col>
          </>
        )}
      </Row>
    </Container>
  );
}
