import React from "react";

import { IoSchoolSharp } from "react-icons/io5";
import { MdOutlineWork } from "react-icons/md";
import { FaMicrochip } from "react-icons/fa6";
import { IoIosBasketball } from "react-icons/io";
import { Col, Container, Row } from "react-bootstrap";

import SkillItem from "./SkillItem";

export default function Skills() {
  const skills = [
    {
      icon: <IoSchoolSharp size={40} />,
      name: "Education",
      description:
        "My journey in the computer science field has been a long road. It started in Urbino during the high school and it ended in Bologna (for now), where I graduated as a Master of Science in Computer Science Engineering. In the meantime, I also landed in Valencia, in Spain, where I attended a 6 months exchange program.",
    },
    {
      icon: <MdOutlineWork size={40} />,
      name: "Profession",
      description:
        "During my professional career I have specialized in Software Engineering. I like to provide scalable, efficient and modular solutions, exploiting Software design patterns and principles. I also like to combine research and development, in order to stay always up to date with the newest innovations.",
    },
    {
      icon: <FaMicrochip size={40} />,
      name: "Research",
      description:
        "Artificial Intelligence is the topic which fascinates me the most. I like studying cutting edge technologies applied to real-world situations. In particular I am interested in everything regarding Machine/Deep Learning, Computer Vision, Natural Language Processing and Augmented, Virtual and Mixed reality.",
    },
    {
      icon: <IoIosBasketball size={40} />,
      name: "Spare time",
      description:
        "My very first passion is Basketball, I started to dribble when I was 6 and I never stopped since then. I see in the sport a good way to distract yourself from work and clearify ideas. In general, I am a fun of the quote 'Healthy body, healthy mind'.",
    },
  ];

  const grid = 12 / skills.length;
  return (
    <Container>
      <Row>
        {skills.map((skill, index) => (
          <Col lg={grid} className="text-center p-2" style={{ marginTop: 0 }}>
            <SkillItem key={index} skill={skill} />
          </Col>
        ))}
      </Row>
    </Container>
  );
}
