import React from "react";
import FilterList from "./FilterList";

export default function ProjectItem({ project }) {
  return (
    <div
      className="rounded"
      style={{ border: "1px solid rgba(0, 0, 0, .125)" }}
    >
      <img
        src={project.cover}
        className="rounded-top"
        style={{ width: "100%" }}
        alt={project.cover}
      />
      <h5 style={{ marginTop: ".5rem" }}>{project.title}</h5>
      <div style={{ paddingRight: ".5rem", paddingLeft: ".5rem" }}>
        <p>{project.description}</p>
      </div>
      <div className="mb-2">
        <FilterList
          list={project.roles}
          background={"#dcecef"}
          color={"#02697b"}
          margin={"m-1"}
        />
        <FilterList
          list={project.skills}
          background={"#fcf2d9"}
          color={"#976c02"}
          margin={"m-1"}
        />
      </div>
      <div
        className="py-3 rounded-bottom"
        style={{ background: "#947458", color: "#f5f5f5" }}
      >
        {project.link ? (
          <a
            href={project.link}
            className="text-decoration-none"
            style={{ color: "#f5f5f5" }}
          >
            Project link
          </a>
        ) : (
          <span>Protected by Non-Disclosure Agreement</span>
        )}
      </div>
    </div>
  );
}
