import React from "react";

import { Navbar, Container } from "react-bootstrap";

import LogoHeader from "../../common/images/logoHeader.png";
import HeaderButton from "../../common/components/HeaderButton";

export default function Header() {
  return (
    <Navbar
      style={{
        flexWrap: "wrap",
        width: "100%",
        borderBottom: "1px solid #dee2e6",
      }}
    >
      <Container style={{ width: "100%" }}>
        <Navbar.Brand className="my-1 header-brand" href="#home">
          <img
            src={LogoHeader}
            alt="Simone Bartoli logo"
            style={{ width: "150px" }}
          />
        </Navbar.Brand>
        <Navbar.Text className="my-1 header-buttons">
          <HeaderButton text="Skills" to="/#skills" color="#947458" size="sm" />
          <HeaderButton
            text="Portfolio"
            to="/#portfolio"
            color="#947458"
            size="sm"
          />
          <HeaderButton
            text="Curriculum"
            to="/CV_Simone_Bartoli.pdf"
            background="#947458"
            color="#f5f5f5"
          />
        </Navbar.Text>
      </Container>
    </Navbar>
  );
}
