import React, { useEffect, useState } from "react";

export default function FilterElement({
  element,
  isSelected,
  onClick,
  clickable,
  background,
  color,
}) {
  const cursor = clickable ? "pointer" : "default";
  const boxShadowStart = isSelected ? `0 0 .7rem ${color}` : "none";
  const [boxShadow, setBoxShadow] = useState(boxShadowStart);

  const onMouseEnter = () => {
    if (clickable && !isSelected) {
      setBoxShadow(`0 0 .7rem ${color}`);
    }
  };

  const onMouseLeave = () => {
    if (clickable && !isSelected) {
      setBoxShadow("none");
    }
  };

  useEffect(() => {}, [isSelected]);

  const elementClick = () => {
    if (!clickable) {
      return;
    }
    onClick();
    isSelected && setBoxShadow("none");
  };

  return (
    <div
      className="rounded"
      onClick={elementClick}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
      style={{
        background: background,
        color: color,
        height: "100%",
        width: "100%",
        minHeight: 50,
        boxShadow: boxShadow,
        cursor: cursor,
        display: "flex",
        paddingRight: "calc(var(--bs-gutter-x)* .5)",
        paddingLeft: "calc(var(--bs-gutter-x)* .5)",
      }}
    >
      <div className="align-self-center text-center m-auto">{element}</div>
    </div>
  );
}
