import React from "react";

import { FaGithub, FaLinkedin } from "react-icons/fa";

import Cover from "../../common/images/profileImage.png";
import { Container } from "react-bootstrap";
import LinkButton from "../../common/components/LinkButton";

export default function Abstract() {
  return (
    <Container className="m-auto text-center px-3" style={{ maxWidth: "100%" }}>
      <div className="mb-4">
        <img
          src={Cover}
          alt="Simone Bartoli"
          style={{
            width: 325,
            maxWidth: "100%",
            border: "1px solid white",
            boxShadow: "10px -10px 10px #0000002f",
            borderRadius: "25px",
          }}
        />
      </div>
      <div className="text-center m-auto" style={{ maxWidth: 500 }}>
        <h1 className="display-5 fw-bold">Simone Bartoli</h1>
        <p className="lead fs-3">
          My passion is to find solutions in order to solve daily repetitive
          problems. It turns out that coding is the best way to do so, that's
          why I became a computer science developer!
        </p>
      </div>
      <div>
        <LinkButton
          to={"https://github.com/simo6"}
          text=" simo6 "
          icon={<FaGithub size={25} className="mx-1" />}
          variant="primary"
        />
        <LinkButton
          to={"https://www.linkedin.com/in/simone-bartoli-a51bb8150/"}
          text=" Simone Bartoli "
          icon={<FaLinkedin size={25} className="mx-1" />}
          variant="outline-secondary"
        />
      </div>
    </Container>
  );
}
