import React from "react";

export default function HeaderButton({
  text,
  to,
  background,
  color,
  size = "lg",
}) {
  const padding = size === "lg" ? "10px 14px" : "10px 10px";
  return (
    <a href={to} className="text-decoration-none mx-2">
      <span
        className="rounded"
        style={{ background: background, color: color, padding: padding }}
      >
        {text}
      </span>
    </a>
  );
}
