import React, { useEffect } from "react";

import { Col, Container, Row } from "react-bootstrap";
import FilterElement from "./FilterElement";

export default function FilterList({
  list,
  selected = [],
  onClick,
  clickable,
  background,
  color,
  margin = "m-2",
}) {
  useEffect(() => {}, [selected, list]);

  return (
    <Container style={{ height: "fit-content", width: "100%" }}>
      <Row>
        {Object.keys(list).map((key, idx) => (
          <Col
            className={`${margin}`}
            key={idx}
            style={{
              width: "100%",
              paddingLeft: 0,
              paddingRight: 0,
            }}
          >
            <FilterElement
              onClick={() => (clickable ? onClick(list[key]) : null)}
              isSelected={selected.includes(list[key])}
              element={list[key]}
              clickable={clickable}
              background={background}
              color={color}
            />
          </Col>
        ))}
      </Row>
    </Container>
  );
}
